
require('../lib/swiper.js')
var Vue = require('../lib/vue.js')
new Vue({
	el:'#home',
	data:{
		list:[]
		
					
	},
	mounted:function(){
			fetch('/api/list').then(response => response.json())
			.then(res => {
				console.log(res)
				this.list = res;
				this.swiper = new Swiper("#index-swiper",{
					loop:true,
					pagination : '.swiper-pagination',
					autoplay:2000,
					autoplayStopOnLast : true,
				})
			})
		}
})


